.header {
    background-repeat: no-repeat;
    background-position:bottom;
    background-attachment: fixed;
    background-image: url('https://res.cloudinary.com/dpikphrtb/image/upload/v1672619157/fondo4_ptrlpz.jpg');
    // background-image: url('../../../fondo/fondo3.jpg');
    background-size: cover;
    z-index: 0;
    
    .contenido-logo {
        padding-top: 0;
        display: flex;
        justify-content: center;
        flex-direction: column; // Pemite ubicar en columnas
        align-items: center; //align-items para centrar el texto verticalmente
        color: $blanco;
        text-transform: uppercase;
        background-color: rgba(0, 0, 0, 0.5);
        @include smartwatch{
            margin: 0;
            height: 65rem;
            width: 100%;
        }
        @include telefono {
            width: 100%;
            height: 50rem;
        }
        @include tablet {
            width: 100%;
            height: 60rem;
        }
        @include desktop {
            width: 100%;
            height: 80rem;
        }
        
        
        
    }

}
.navegacion-principal{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-size: 4rem;
    background-color: $grisClaro;
    padding: 2rem;
    padding-top: 0;

    @include smartwatch{
        display: flex;
        flex-direction: column;
        align-items: center;
        font-size: 2.5rem;
        border-bottom: 1px solid $grisClaro;
    }
    @include telefono {
        display: flex;
        flex-direction: column;
        text-align: center;
        padding: 0 auto;
        row-gap: 2rem;
        font-size: 3rem;
        
    }
    @include tablet {
        display: flex;
        flex-direction: inherit;
        justify-content: space-evenly;
        font-size: 4rem;
    }
    @include desktop {
        display: flex;
        flex-direction: inherit;
        justify-content: space-evenly;
        font-size: 4rem;
    }
    .link {
        color: $negro;
        text-decoration: none;
        text-transform: uppercase;
        font-weight: $delgada;
        &:hover{
            cursor: pointer;
            color: rgb(255, 0, 0);
        }
    }
  }
.logo-small{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    background-color: $grisClaro;
    border-bottom: .1px solid $grisOscuro;
    #image-small{
        padding-top: 2rem;
        padding-bottom: 2rem;
        @include smartwatch{
            width: 28%;
        }
        @include telefono{
            width: 20%;
        }
        @include tablet{
            width: 15%;
        }
        @include desktop{
            width: 10%;
        }
    }
}
