
.nosotros{
    background-repeat: no-repeat;
    background-position:bottom;
    background-attachment: fixed;
    background-image: url('https://res.cloudinary.com/dpikphrtb/image/upload/v1672604491/fondo1_qwuqli.jpg');
    // background-image: url('../../../fondo/fondo1.jpg');
    background-size: cover;
    font-weight: $delgada;
    color: $blanco;

    .contenido-nosotros {
        overflow:hidden;
        text-align: center;
        font-family: $fuentePrincipal;
        
        h3{
            font-family: $fuenteSecundario;
            font-weight: $delgada;
            font-size: 5rem;
        }
        p{
            color: $blanco;
            font-size: 2rem;
            
        }
   
        #nosotros-selector{
            border: 1px solid $blanco;
            font-weight: $delgada;
            font-size: 3rem;
            
        }
    }
}