.image-bg-formulario{
    background-repeat: no-repeat;
    background-position:bottom;
    background-attachment: fixed;
    background-image: url('https://res.cloudinary.com/dpikphrtb/image/upload/v1672619157/fondo4_ptrlpz.jpg');
    // background-image: url('../../../fondo/fondo3.jpg');
    background-size: cover;
    
    height: 80rem;
    h2{
        text-align: center;
        padding: 5rem 0;
        font-family: fuenteSecundario;
        font-weight: $delgada;
        color: $blanco;
        font-size: 5rem;
    }
    
}
.bg-color{
    background-color: rgba(0, 0, 0, 0.5);
    height: 80rem;
}
.formulario {
    margin-bottom: 20rem;
    width: 95%;
}
.campo {
    display: flex;
    margin-bottom: 2rem;
    height: 5rem;
    margin: 4rem;

}
.campo__field {
    border-radius: 0.9rem;
    flex: 2;
    border-top: 1px solid $blanco;
    border-left: 1px solid $blanco;
    border-color: $blanco;
    border-width: 1px;
    &:hover{
        border-color: $rojo;
    }

}
.campo__field--textarea {
    height: 15rem;
    color: $blanco;
    &:hover{
        border-color: $rojo;
    }

}
* {
    background-color: transparent;
    border-color: $blanco;

}
.btn-enviar{
    
    display: flex;
    justify-content: center;    
    align-items: center;
    margin-bottom: 0;
    input{
        color: $blanco;
        font-family: $fuenteSecundario;
        background-color:$negro ;
        font-weight: $delgada;
        font-size: 2rem;
        padding: 1rem 3rem ;
        border-radius: 3rem;
        border-style: 1px solid $blanco;
        border-bottom: 1px solid $blanco;
        border-right: 1px solid $blanco;
        &:hover{
            background-color: $rojo;
        }
    }

}

*:focus{
    outline: none;
}
