html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  height: 100%;
  margin: 0;
}

p {
  font-size: 2rem;
  color: #000000;
}

.contenedor {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
}

p {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1 {
  font-size: 4rem;
  font-weight: 700;
  margin-top: 0;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.header {
  background-repeat: no-repeat;
  background-position: bottom;
  background-attachment: fixed;
  background-image: url("https://res.cloudinary.com/dpikphrtb/image/upload/v1672619157/fondo4_ptrlpz.jpg");
  background-size: cover;
  z-index: 0;
}
.header .contenido-logo {
  padding-top: 0;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  color: #FFF;
  text-transform: uppercase;
  background-color: rgba(0, 0, 0, 0.5);
}
@media (min-width: 250px) {
  .header .contenido-logo {
    margin: 0;
    height: 65rem;
    width: 100%;
  }
}
@media (min-width: 468px) {
  .header .contenido-logo {
    width: 100%;
    height: 50rem;
  }
}
@media (min-width: 850px) {
  .header .contenido-logo {
    width: 100%;
    height: 60rem;
  }
}
@media (min-width: 1400px) {
  .header .contenido-logo {
    width: 100%;
    height: 80rem;
  }
}

.navegacion-principal {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-size: 4rem;
  background-color: #eeeeee;
  padding: 2rem;
  padding-top: 0;
}
@media (min-width: 250px) {
  .navegacion-principal {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 2.5rem;
    border-bottom: 1px solid #eeeeee;
  }
}
@media (min-width: 468px) {
  .navegacion-principal {
    display: flex;
    flex-direction: column;
    text-align: center;
    padding: 0 auto;
    row-gap: 2rem;
    font-size: 3rem;
  }
}
@media (min-width: 850px) {
  .navegacion-principal {
    display: flex;
    flex-direction: inherit;
    justify-content: space-evenly;
    font-size: 4rem;
  }
}
@media (min-width: 1400px) {
  .navegacion-principal {
    display: flex;
    flex-direction: inherit;
    justify-content: space-evenly;
    font-size: 4rem;
  }
}
.navegacion-principal .link {
  color: #000000;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 300;
}
.navegacion-principal .link:hover {
  cursor: pointer;
  color: rgb(255, 0, 0);
}

.logo-small {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  background-color: #eeeeee;
  border-bottom: 0.1px solid #DCDCDC;
}
.logo-small #image-small {
  padding-top: 2rem;
  padding-bottom: 2rem;
}
@media (min-width: 250px) {
  .logo-small #image-small {
    width: 28%;
  }
}
@media (min-width: 468px) {
  .logo-small #image-small {
    width: 20%;
  }
}
@media (min-width: 850px) {
  .logo-small #image-small {
    width: 15%;
  }
}
@media (min-width: 1400px) {
  .logo-small #image-small {
    width: 10%;
  }
}

