.opacar {
  background: blue;
  transition: all 2s;
}
.opacar:hover {
  background: black;
}

.body-Color {
  background: #FFF;
  transition: all 3s;
}

.body-Color:hover {
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#ffffff+34,f3f3f3+100,ededed+100,ffffff+100 */
  background: rgb(255, 255, 255); /* Old browsers */
  background: -moz-linear-gradient(top, rgb(255, 255, 255) 34%, rgb(243, 243, 243) 100%, rgb(237, 237, 237) 100%, rgb(255, 255, 255) 100%); /* FF3.6-15 */
  background: -webkit-linear-gradient(top, rgb(255, 255, 255) 34%, rgb(243, 243, 243) 100%, rgb(237, 237, 237) 100%, rgb(255, 255, 255) 100%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, rgb(255, 255, 255) 34%, rgb(243, 243, 243) 100%, rgb(237, 237, 237) 100%, rgb(255, 255, 255) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#ffffff", endColorstr="#ffffff",GradientType=0 ); /* IE6-9 */
}

