  margin-bottom: 20rem;
  width: 95%;
}

.campo {
  display: flex;
  margin-bottom: 2rem;
  height: 5rem;
  margin: 4rem;
}

.campo__field {
  border-radius: 0.9rem;
  flex: 2;
  border-top: 1px solid #FFF;
  border-left: 1px solid #FFF;
  border-color: #FFF;
  border-width: 1px;
}
.campo__field:hover {
  border-color: rgb(177, 43, 43);
}

.campo__field--textarea {
  height: 15rem;
  color: #FFF;
}
.campo__field--textarea:hover {
  border-color: rgb(177, 43, 43);
}

* {
  background-color: transparent;
  border-color: #FFF;
}

.btn-enviar {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 0;
}
.btn-enviar input {
  color: #FFF;
  font-family: "Playfair Display", serif;
  background-color: #000000;
  font-weight: 300;
  font-size: 2rem;
  padding: 1rem 3rem;
  border-radius: 3rem;
  border-style: 1px solid #FFF;
  border-bottom: 1px solid #FFF;
  border-right: 1px solid #FFF;
}
.btn-enviar input:hover {
  background-color: rgb(177, 43, 43);
}

*:focus {
  outline: none;
}

.footer {
  background-color: #DCDCDC;
  padding: 2.5rem;
  text-align: center;
}
.footer p {
  font-size: 3rem;
  font-family: "Open Sans", sans-serif;
  font-weight: 300;
  color: #000000;
  margin: 0;
}